<script>
  import { DataTable, Dialog, Button } from "smelte";
  import Icon from "svelte-icon";
  import csv from "../svg/csv.svg";
  import excel from "../svg/excel.svg";
  import { exportExcel, print, csvGenerator } from "../export.js";
  let showDialog = false;
  export let title;
  export let columns;
  export let rows;

  import { get } from "svelte/store";
  import { valuesForm, Field } from "svelte-formly";

  const fields = [
    {
      type: "color",
      name: "color",
      id: "color",
      label: "Color Form",
    },
    {
      type: "text",
      name: "firstname",
      value: "",
      id: "firstname",
      class: ["form-control"],
      placeholder: "Tap your first name",
      validation: ["required", "min:6"],
      messages: {
        required: "Firstname field is required!",
        min: "First name field must have more that 6 caracters!",
      },
    },
    {
      prefix: {
        class: ["custom-form-group"],
      },
      type: "text",
      name: "lastname",
      value: "",
      id: "lastname",
      placeholder: "Tap your lastname",
      description: {
        class: ["custom-class-desc"],
        text: "Custom text for description",
      },
    },
    {
      type: "email",
      name: "email",
      value: "",
      id: "email",
      placeholder: "Tap your email",
      validation: ["required", "email"],
    },
    {
      type: "radio",
      name: "gender",
      items: [
        {
          id: "female",
          value: "female",
          title: "Female",
        },
        {
          id: "male",
          value: "male",
          title: "Male",
        },
      ],
    },
    {
      type: "select",
      name: "city",
      id: "city",
      label: "City",
      validation: ["required"],
      options: [
        {
          value: 1,
          title: "Agadir",
        },
        {
          value: 2,
          title: "Casablanca",
        },
      ],
    },
  ];

  let message = "";
  let values = {};
  let color = "#ff3e00";

  function onSubmit() {
    const data = get(valuesForm);
    if (data.isValidForm) {
      values = data.values;
      color = values.color ? values.color : color;
      message = "Congratulation! now your form is valid";
    } else {
      message = "Your form is not valid!";
    }
  }
</script>

<h3>{title}</h3>
<DataTable data={rows} {columns} />
<Dialog persistent="true" bind:value={showDialog}>
  <form
    on:submit|preventDefault={onSubmit}
    class="custom-form"
    style="--theme-color: {color}">
    <Field {fields} />
    <button class="btn btn-primary" type="submit">Submit</button>
  </form>
  <div slot="actions">
    <Button text on:click={() => (showDialog = false)}>Disagree</Button>
    <Button text on:click={() => (showDialog = false)}>Agree</Button>
  </div>
</Dialog>

<div class="py-2">
  <Button on:click={() => (showDialog = true)}>Show dialog</Button>
</div>

<div class="actions md:flex">
  <a
    href="#!"
    class="waves-effect btn-flat nopadding"
    on:click={() => print(columns, rows)}>
    <i class="material-icons text-error-500">print</i>

  </a>
  <a
    href="#!"
    class="waves-effect btn-flat nopadding"
    v-if="this.exportable"
    on:click={() => exportExcel(columns, rows, title)}>
    <Icon data={excel} color="#67efef" size="24px" />
  </a>

  <button on:click={() => csvGenerator(rows, columns, title)}>
    <Icon data={csv} color="#897878" size="24px" />
  </button>
</div>
<div class="w-full max-w-xs">
  <form class="bg-white shadow-md rounded px-8 pt-6 pb-8 mb-4">
    <div class="mb-4">
      <label class="block text-gray-700 text-sm font-bold mb-2" for="username">
        Username
      </label>
      <input class="shadow appearance-none border rounded w-full py-2 px-3 text-gray-700 leading-tight focus:outline-none focus:shadow-outline" id="username" type="text" placeholder="Username">
    </div>
    <div class="mb-6">
      <label class="block text-gray-700 text-sm font-bold mb-2" for="password">
        Password
      </label>
      <input class="shadow appearance-none border border-red-500 rounded w-full py-2 px-3 text-gray-700 mb-3 leading-tight focus:outline-none focus:shadow-outline" id="password" type="password" placeholder="******************">
      <p class="text-red-500 text-xs italic">Please choose a password.</p>
    </div>
    <div class="flex items-center justify-between">
      <button class="bg-blue-500 hover:bg-blue-700 text-white font-bold py-2 px-4 rounded focus:outline-none focus:shadow-outline" type="button">
        Sign In
      </button>
      <a class="inline-block align-baseline font-bold text-sm text-blue-500 hover:text-blue-800" href="#">
        Forgot Password?
      </a>
    </div>
  </form>
  <p class="text-center text-gray-500 text-xs">
    &copy;2020 Acme Corp. All rights reserved.
  </p>
</div>