<script>
  import FormData from "./componenet/FormData.svelte";
  import datas from "./data.json";

  const columns = [
    { label: "ID", field: "id", class: "w-10" },
    {
      label: "Season/Episode",
      // field: "season",
      value: (v) => `S${v.season}E${v.number}`,
      class: "w-10",
    },
    { label: "Name", field: "name", class: "w-10" },
    {
      label: "Summary",
      field: "summary",
      value: (v) => (v && v.summary ? v.summary : ""),
      class: "text-sm text-gray-700 caption w-full",
    },
    {
      label: "Thumbnail",
      field: "thumbnail",
      value: (v) =>
        v && v.image
          ? `<img src="${v.image.medium.replace(
              "http",
              "https"
            )}" height="70" alt="${v.name}">`
          : "",
      class: "w-48",
      sortable: false,
    },
  ];
  const rows = datas._embedded.episodes;
</script>

<FormData {rows} {columns} title="testing" />
